import { Button,FormControl,Input,InputLabel } from '@material-ui/core';
import { useState,useEffect } from 'react';
import './App.css';
import db from './firebase';
import Message from './Message';
import firebase from 'firebase'
import FlipMove from 'react-flip-move'
import SendIcon from '@material-ui/icons/Send'
import { IconButton } from '@material-ui/core';

function App() {

  const [input,setInput] = useState('')
  const [messages,setMessages] = useState([])
  const [username,setUsername] = useState('')

  const sendMessage = e => {
    e.preventDefault()
    db.collection('messages').add({
      username,
      message:input,
      timestamp:firebase.firestore.FieldValue.serverTimestamp()
    })
   setInput('')
  }

  const handleChange = e => {
    
    setInput(e.target.value)
  }

   useEffect(()=>{
    setUsername(prompt('Please enter you name :'))
  },[])

useEffect(()=>{
 db.collection('messages')
 .orderBy('timestamp','desc')
 .onSnapshot(snapshot =>{
   setMessages(snapshot.docs.map(doc => ({id:doc.id,message:doc.data()})))
 })
},[])

 
  
  return (
    <div className="App">
      <img className='messenger-icon' src="data:image/svg+xml;base64," alt="" />
      <h1>Hello {username}</h1>

      <form className='app_form'>
       <FormControl className='app_formControl'>
  
  <Input className='app_input' placeholder='Enter a message' value={input} onChange={handleChange} aria-describedby="my-helper-text" />

<IconButton className='app_iconButton' type='submit' disabled={!input}variant='contained' color='primary'onClick={ sendMessage}>
  <SendIcon/>
</IconButton>



 
</FormControl>
      </form>

<FlipMove>
      {
        messages.map(({message,id}) =>(
          <Message key={id} message={message} username={username}/>
          
        ))
      }
      </FlipMove>
    </div>
  );
}

export default App;
